import { Option } from "../../../base/components/native/option";
import { Select } from "../../../base/components/native/select"
import * as styles from './language-select.style'

const LANGUAGES: any[] = [
    { code: 'en-US', name: 'English (US)', nativeName: 'English' },
    { code: 'nl-NL', name: 'Dutch', nativeName: 'Nederlands' },
    { code: 'de-DE', name: 'German', nativeName: 'Deutsch' },
    { code: 'fr-FR', name: 'French', nativeName: 'Franais' },
    { code: 'es-ES', name: 'Spanish', nativeName: 'Espaol' },
    { code: 'it-IT', name: 'Italian', nativeName: 'Italiano' },
    { code: 'zh-CN', name: 'Chinese (Simplified)', nativeName: '' },
    { code: 'sv-SE', name: 'Swedish', nativeName: 'Svenska' },
    { code: 'ja-JP', name: 'Japanese', nativeName: '' },
    { code: 'hi-IN', name: 'Hindi', nativeName: '' },
    { code: 'ar-SA', name: 'Arabic', nativeName: '' },
    { code: 'pt-BR', name: 'Portuguese (Brazil)', nativeName: 'Portugus' },
    { code: 'ru-RU', name: 'Russian', nativeName: '' },
    { code: 'ko-KR', name: 'Korean', nativeName: '' },
    { code: 'tr-TR', name: 'Turkish', nativeName: 'Trke' },
    { code: 'pl-PL', name: 'Polish', nativeName: 'Polski' },
    { code: 'vi-VN', name: 'Vietnamese', nativeName: 'Ting Vit' },
    { code: 'th-TH', name: 'Thai', nativeName: '' },
    { code: 'id-ID', name: 'Indonesian', nativeName: 'Bahasa Indonesia' },
    { code: 'cs-CZ', name: 'Czech', nativeName: 'etina' }
];

export const LanguageSelect = () => {
    const base = Select()
    base.cssClass(styles.baseStyle)

    LANGUAGES.forEach(lang => {
        const option = Option(lang.code, lang.name)
        base.append(option)
    })

    return Object.assign(base, {
        getValue: () => {
            return base.el.value
        },
        setValue: (val: string) => {
            base.el.value = val
        }
    })
}
